import {Interface, type} from '@appAssets/externals/implement';
/**
 * Interface used to bind each endPoint.
 */
export interface POTIEndpoint {
    url: string;
    mockLocation: string;
    mocked: boolean;
    httpMethod: string;
    requiresAuth: boolean;
}
/**
 * Used to validate if a object implements the {@link POTIEndPoint} interface. If it doesn't it will throw an Error.
 */
export const POTEndpointValidator = Interface('POTIEndpoint')({
    url: type('string'),
    mockLocation: type('string'),
    mocked: type('boolean'),
    httpMethod: type('string'),
    requiresAuth: type('boolean'),
}, { error: true, trim: true });
