import { Component, Input, OnInit, OnDestroy } from '@angular/core';
/**
 * Creates the pot animated logo with the text
 */
@Component({
  selector: 'app-wait',
  templateUrl: './wait.component.html',
  styleUrls: ['./wait.component.scss']
})

export class WaitComponent implements OnInit, OnDestroy {
  /**
 * Action(text) to be painted under the logo
 */
  @Input() action: string;
  /**
 * Promise to know when the image has finished loading
 */
  imageLoaded: Promise<boolean>;
  /**
 * @ignore
 */
  constructor() { }
/**
 * @ignore
 */
  ngOnInit() {
  }
/**
 * Called when image has finished rendering on view. Resolves the promise
 */
  loadText() {
    this.imageLoaded = Promise.resolve(true);
  }
/**
 * @ignore
 */
  ngOnDestroy() {}

}
