import { ModalData } from '@appInyectables/POT.modal-data.inyectable';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { POTUserService } from '@appServices/POT.user.service';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { POTFormValidators } from '@appUtils/POT.validators';
import { POTUserModel } from '@appModels/POT.user.model';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
/**
 * Component inside modal to allow user registration
 */
@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.scss']
})
export class RegistrationComponent implements OnInit {
  @Output() EmitResult: EventEmitter<any> = new EventEmitter();
  /**
 * @ignore
 */
  protected log = POTAppConfig.logFactory.getLogger('log.component.registration');
  /**
 * @angular/forms element to control the form's elements
 */
  registrationForm: FormGroup;
  /**
   * @param  {UserService} privateuserServive
   * @param  {FormBuilder} privateformBuilder
   * @param  {ModalData} privatecommonData
   * @param  {NgbActiveModal} privateactiveModal
   */
  constructor(private userServive: POTUserService,
    private formBuilder: FormBuilder, private commonData: ModalData, private ngbActiveModal: NgbActiveModal) { }

  ngOnInit() {
    this.registrationForm = this.formBuilder.group({
      emailInput: [null, [Validators.required, Validators.compose([Validators.minLength(5), Validators.email])]],
      usernameInput: [null, [Validators.required, Validators.minLength(5)]],
      passwordInput: [null, Validators.compose([
        Validators.required,
        Validators.minLength(8),
        // check whether the entered password has a number
        POTFormValidators.regExValidation(/\d/, {
          hasNumber: true
        }),
        // check whether the entered password has upper case letter
        POTFormValidators.regExValidation(/[A-Z]/, {
          hasCapitalCase: true
        }),
        // check whether the entered password has a lower case letter
        POTFormValidators.regExValidation(/[a-z]/, {
          hasSmallCase: true
        }),
        // check whether the entered password has a special character
        POTFormValidators.regExValidation(
          /[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
          {
            hasSpecialCharacters: true
          }
        )
      ])],
      repeatPasswordInput: [null, Validators.compose([Validators.required])]
    }, { validator: POTFormValidators.samePasswordValidation });
  }

  get fControl(): { [key: string]: AbstractControl; } { return this.registrationForm.controls; }

  get validForm(): boolean { return this.registrationForm.valid; }
  /**
   * Registration method after all the inputs are OK
   */
  register() {
    if (this.registrationForm.invalid) {
      this.log.error('Invalid data on registrationForm, please try again.');
      return;
    }
    try {
      const newUser = new POTUserModel();
      newUser.email = this.fControl.emailInput.value;
      newUser.username = this.fControl.usernameInput.value;
      newUser.password = this.fControl.passwordInput.value;
      this.userServive.registration(newUser).then(() => {
        this.commonData.setResponseOK(newUser);
        this.log.debug(`registration ok, emiting: ${JSON.stringify(newUser)}`);
        this.emitValue(newUser);
      }).catch((error) => {
        throw error;
      });
    } catch (error) {
      this.log.error(error);
    }
  }

  private emitValue(value: any) {
    this.EmitResult.emit(value);
    this.ngbActiveModal.close();
  }
}
