import { POTAvailableErrorTypes } from '@appUtils/POT.enums';

export class POTPromiseError extends Error {
    constructor(message) {
        super(message);
        this.name = POTAvailableErrorTypes.PromiseError;
    }
}

export class POTValidationError extends Error {
    constructor(message) {
        super(message);
        this.name = POTAvailableErrorTypes.ValidationError;
    }
}

export class POTConfigurationError extends Error {
    constructor(message) {
        super(message);
        this.name = POTAvailableErrorTypes.ConfigurationError;
    }
}

export class POTGenericError extends Error {
    constructor(message: string) {
        super(message);
        this.name = POTAvailableErrorTypes.GenericError;
    }
}

export class POTHttpRequestError extends Error {
    constructor(message) {
        super(message);
        this.name = POTAvailableErrorTypes.HttpRequestError;
    }
}

export class POTModalError extends Error {
    constructor(message) {
        super(message);
        this.name = POTAvailableErrorTypes.ModalError;
    }
}
