import { POTValidationError } from './../utils/POT.errors';
import { POTStorageService } from '@appServices/POT.storage.service';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { POTBaseRoutes } from '@appUtils/POT.enums';
import { POTUserModel } from '@appModels/POT.user.model';

@Injectable()
export class POTAuthGuard implements CanActivate {
    private loggedUser: POTUserModel;
    protected log = POTAppConfig.logFactory.getLogger('log.authguard');
    constructor(private router: Router, private storageService: POTStorageService) { }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        try {
            const loggedUserObservable = this.storageService.loggedUser;
            loggedUserObservable.subscribe((value) => {
                this.loggedUser = value;
            });
            // TODO: Validate routing here
            if (this.loggedUser.role !== 'admin') {
                // home/*
                const homeValid = this.checkIfValid('home', 'username', state);
                // users/*
                const usersValid = this.checkIfValid('users', 'username', state);
                // sites/*
                const sitesValid = this.checkIfValid('sites', 'username', state);
                // rules/*
                const rulesValid = this.checkIfValid('rules', 'username', state);
                if (!homeValid || !usersValid || !sitesValid || !rulesValid) {
                    throw new POTValidationError(`You dont have authorization to navigate to ${state.url}`);
                }
            }
            this.log.debug('Authguard gives ok to navigate to: ' + state.url);
            return true;
        } catch (error) {
            this.log.error(error);
        }
        this.log.debug('Authguard blocked navigation to: ' + state.url);
        this.router.navigate([POTBaseRoutes.DEFAULT]);
        return false;
    }

    private checkIfValid(subURL: string, key: string, state: RouterStateSnapshot) {
        const includesUrl = state.url.includes(`/${subURL}/`);
        if (includesUrl) {
            const valueOnUrl = state.url.split(`/`)[2];
            return this.loggedUser[key] === valueOnUrl;
        }
        return true;
    }
}
