import { POTIModalResponse } from '@appInterfaces/POT.modal-response.interface';
import { Observable, AsyncSubject } from 'rxjs';
import { Injectable, OnDestroy } from '@angular/core';
import { POTModalTypes, POTModalStatusTypes } from '@appUtils/POT.enums';
import { POTModalConfiguration } from '@appModels/POT.modal-configuration.model';

/**
* Injectable used by all modal components.
*/
@Injectable()
export class ModalData implements OnDestroy {
    private configurationObservable: Observable<POTModalConfiguration> = null;
    /**
    * AsyncSubject to inform when the modal logic is finished (usefull for modal components)
    */
    private responseAsync: AsyncSubject<POTIModalResponse> = null;
    private activeModalConfiguration: POTModalConfiguration = null;
    /**
    * @ignore
    */
    constructor() { }
    /**
    * Sets observable of type {@link POTModalConfiguration} to bind the modal configuration
    */
    setConfiguration(configurationObservable: Observable<POTModalConfiguration>): void {
        this.configurationObservable = configurationObservable;
        this.configurationObservable.subscribe((data) => {
            this.activeModalConfiguration = data;
        });
        if (this.responseAsync === null || this.responseAsync.closed) {
            this.responseAsync = new AsyncSubject();
        }
    }
    /**
    * Sets observable of type {@link POTModalConfiguration} to bind the modal configuration
    */
    get configuration(): Observable<POTModalConfiguration> {
        return this.configurationObservable;
    }

    get activeConfiguration(): POTModalConfiguration {
        return this.activeModalConfiguration;
    }

    get typeModal(): POTModalTypes {
        return POTModalTypes.MODAL;
    }

    get typePopup(): POTModalTypes {
        return POTModalTypes.POPUP;
    }

    get statusWaiting(): POTModalStatusTypes {
        return POTModalStatusTypes.WAITING;
    }

    get statusOK(): POTModalStatusTypes {
        return POTModalStatusTypes.OK;
    }

    get statusKO(): POTModalStatusTypes {
        return POTModalStatusTypes.KO;
    }

    get response(): Observable<POTIModalResponse> {
        return this.responseAsync.asObservable();
    }

    /**
    * Set the AsyncSubject to {@link POTModalResponse.OK} and completes the subject.
    */
    setResponseOK(responseData: any): void {
        const response: POTIModalResponse = {
            data: responseData,
            status: POTModalStatusTypes.OK
        };
        this.setResponseAsyncValue(response);
    }
    /**
    * Set the AsyncSubject to {@link POTModalResponse.KO} and completes the subject.
    */
    setResponseKO(responseData: any): void {
        const response: POTIModalResponse = {
            data: responseData,
            status: POTModalStatusTypes.KO
        };
        this.setResponseAsyncValue(response);
    }
    /**
   * Set the AsyncSubject to {@link POTModalResponse.CLOSE} and completes the subject.
   */
    setResponseCLOSE(responseData: any): void {
        const response: POTIModalResponse = {
            data: responseData,
            status: POTModalStatusTypes.CLOSED
        };
        this.setResponseAsyncValue(response);
    }
    /**
  * Set the AsyncSubject to {@link POTModalResponse.DISMISSED} and completes the subject.
  */
    setResponseDISMISSED(responseData: any): void {
        const response: POTIModalResponse = {
            data: responseData,
            status: POTModalStatusTypes.DISMISSED
        };
        this.setResponseAsyncValue(response);
    }

    private setResponseAsyncValue(response) {
        if (this.responseAsync !== null) {
            this.responseAsync.next(response);
            this.responseAsync.complete();
            this.clearData();
        }
    }

    /**
    * @ignore
    */
    clearData(): void {
        this.configurationObservable = null;
        this.responseAsync = null;
    }

    ngOnDestroy() {

    }
}
