import { AbstractLogger, LogMessage } from 'typescript-logging';
import { LogGroupRuntimeSettings } from 'typescript-logging/dist/commonjs/log/standard/LogGroupRuntimeSettings';
import { environment } from '@appEnvironments/environment';

export class POTLoger extends AbstractLogger {
    constructor(name: string, settings: LogGroupRuntimeSettings) {
        super(name, settings);
    }

    protected doLog(msg: LogMessage): void {
        if (environment.showLog) {
            let message;
            if (typeof msg.message === 'string') {
                message = this.createDefaultLogMessage(msg);
            } else {
                message = this.createCustomLogMessage(msg);
            }
            const messageLevel = msg.level;
            switch (messageLevel) {
                case 0:
                    if (this.isTraceEnabled()) {
                        console.log(message);
                    }
                    break;
                case 1:
                    if (this.isDebugEnabled()) {
                        console.log(message);
                    }
                    break;
                case 2:
                    if (this.isInfoEnabled()) {
                        console.log(message);
                    }
                    break;
                case 3:
                    if (this.isWarnEnabled()) {
                        console.warn(message);
                    }
                    break;
                case 4:
                    if (this.isErrorEnabled()) {
                        console.error(message);
                    }
                    break;
                case 5:
                    if (this.isFatalEnabled()) {
                        console.error(message);
                    }
                    break;
            }
        }
    }

    protected createCustomLogMessage(msg: LogMessage): string {
        let message: string;
        const date = new Date(msg.date);
        // tslint:disable-next-line:max-line-length
        const dateString = `${date.getFullYear()}-${date.getMonth()}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()},${date.getMilliseconds()}`;
        let level;
        switch (msg.level) {
            case 0:
                level = 'TRACE';
                break;
            case 1:
                level = 'DEBUG';
                break;
            case 2:
                level = 'INFO';
                break;
            case 3:
                level = 'WARNING';
                break;
            case 4:
                level = 'ERROR';
                break;
            case 5:
                level = 'FATAL';
                break;
            default:
                break;
        }
        const loggerName = `[${msg.loggerName.toString()}]`;
        let stack: any = msg.message;
        if (stack.message) {
            stack = stack.message;
        }
        message = `${dateString} ${level} ${loggerName} ${stack}`;
        return message;
    }
}
