import { POTModalConfiguration } from '@appModels/POT.modal-configuration.model';
import { POTStorageService } from '@appServices/POT.storage.service';
import { POTRouterService } from '@appServices/POT.router.service';
import { POTModalAvailableComponents, POTModalTypes, POTAvailableErrorTypes, POTModalStatusTypes } from '@appUtils/POT.enums';
import { POTUserModel } from '@appModels/POT.user.model';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { POTUserService } from '@appServices/POT.user.service';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { POTFade } from '@appUtils/POT.animations';
import { POTModalService } from '@appServices/POT.modal.service';
/**
 * The login component
 */
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  animations: [POTFade]
})

export class LoginComponent implements OnInit {
  /**
 * @ignore
 */
  private log = POTAppConfig.logFactory.getLogger('log.component.login');
  /**
 * A @angular/forms instance to control the login's form elements
 */
  loginForm: FormGroup;
  /**
 * Boolean used to paint the errors on each input
 */
  submited = false;
  /**
 * Promise used to know when the page has finished loading
 */
  pageAvailable: Promise<boolean>;
  /**
   * @param  {FormBuilder} formBuilder
   * @param  {UserService} userService
   * @param  {ModalService} modalService
   * @param  {RouterService} routerService
   * @param  {StorageService} privatestorageService
   */
  constructor(
    private formBuilder: FormBuilder,
    private userService: POTUserService,
    private modalService: POTModalService,
    private routerService: POTRouterService,
    private storageService: POTStorageService,
    private cdr: ChangeDetectorRef
  ) { }
  /**
   * Used to set the flag when page has finished loading @param  {ms}
   */
  private async waitUntilLoad(ms) {
    await this.sleep(ms).then(() => this.pageAvailable = Promise.resolve(true));
  }

  private sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      usernameInput: ['', [Validators.required, Validators.minLength(5)]],
      passwordInput: ['', [Validators.required, Validators.minLength(8)]]
    });
    try {
      const loggedUser = this.storageService.loggedUser;
      this.routerService.redirectToHome();
    } catch (error) { }
    this.waitUntilLoad(3 * 1000);
  }

  get fControl(): { [key: string]: AbstractControl; } { return this.loginForm.controls; }
  /**
   * Login method
   */
  login() {
    this.submited = true;
    this.cdr.detectChanges();
    if (this.loginForm.invalid) {
      this.log.error('Invalid data on loginForm, please try again.');
      return;
    }
    const user: POTUserModel = new POTUserModel;
    user.username = this.fControl.usernameInput.value;
    user.password = this.fControl.passwordInput.value;
    try {
      this.doLogin(user);
    } catch (error) {
      throw error;
    }
    this.cdr.detectChanges();
  }
  /**
   * Registration method
   */
  registration() {
    const modalConfig = new POTModalConfiguration();
    modalConfig.title = 'Registration';
    modalConfig.modalType = POTModalTypes.MODAL;
    modalConfig.component = POTModalAvailableComponents.REGISTRATION;
    this.modalService.openModal(modalConfig).then((value) => {
      if (value.status === POTModalStatusTypes.OK) {
        const newUser = new POTUserModel().deserialize(value.data);
        this.doLogin(newUser);
      }
    });
  }

  private doLogin(user) {
    this.userService.login(user).then(() => {
      this.routerService.redirectToHome();
    }).catch((error) => {
      throw error;
    });
  }
}
