import { ValidatorFn, ValidationErrors, AbstractControl } from '@angular/forms';

export class POTFormValidators {
    static regExValidation(regEx: RegExp, error: ValidationErrors): ValidatorFn {
        return (control: AbstractControl): { [key: string]: any } => {
            const valid = regEx.test(control.value);
            return valid ? null : error;
        };
    }

    static samePasswordValidation(control: AbstractControl) {
        const pass = control.get('passwordInput').value;
        const confirmPass = control.get('repeatPasswordInput').value;
        if (pass !== confirmPass) {
            control.get('repeatPasswordInput').setErrors({ notSamePass: true });
        }
    }
}
