import { AppComponent } from '@appComponents/app.component';
import { POTSharedModule } from '@appModules/POT.shared.module';
import { POTConfigurationError } from '@appUtils/POT.errors';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { APP_INITIALIZER } from '@angular/core';
import { WebStorageModule } from 'ngx-store';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { POTErrorInterceptor } from '@appInyectables/POT.error-interceptor.inyectable';
import { POTTimeoutInterceptor, POTRequestInterceptor, POTResponseInterceptor } from '@appInyectables/POT.http-interceptors.inyectable';

export function initializeApp(appConfig: POTAppConfig) {
  return () => appConfig.load().catch((error) => {
    console.error(error);
   throw new POTConfigurationError(error);
  });
}

@NgModule({
  imports: [
    BrowserModule.withServerTransition({ appId: 'SEO-POT' }),
    POTSharedModule,
    HttpClientModule,
    WebStorageModule
  ],
  declarations: [
  ],
  providers: [
    {
      provide: APP_INITIALIZER,
      useFactory: initializeApp,
      deps: [POTAppConfig],
      multi: true
    },
    {
      provide: ErrorHandler,
      useClass: POTErrorInterceptor
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: POTTimeoutInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: POTRequestInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: POTResponseInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})

export class AppModule { }
