import { POTIAppConfig, POTAppConfigValidator } from '@appInterfaces/POT.app-config.interface';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { environment } from '@appEnvironments/environment';
import { Injectable, OnDestroy } from '@angular/core';
// tslint:disable-next-line:max-line-length
import { LoggerFactoryOptions, LogGroupRule, LogLevel, LoggerFactory, LFService, LogFormat, LoggerType } from 'typescript-logging';
import { LogGroupRuntimeSettings } from 'typescript-logging/dist/commonjs/log/standard/LogGroupRuntimeSettings';
import { POTLoger } from '@appUtils/POT.loger';
import implement from '@appAssets/externals/implement';

@Injectable({ providedIn: 'root' })
export class POTAppConfig implements OnDestroy {
    static settings: POTIAppConfig;
    static logFactory: LoggerFactory;
    constructor(private http: HttpClient) { }

    load() {
        const jsonFile = environment.file;
        return new Promise<void>((resolve, reject) => {
            this.http.get<POTIAppConfig>(jsonFile, {
                observe: 'response'
            }).toPromise().then((response: HttpResponse<POTIAppConfig>) => {
                if (response.status === 200) {
                    try {
                        this.setLogFactory();
                        const _log = POTAppConfig.logFactory.getLogger('log.app.config');
                        const tempo = Object.assign({}, response.body);
                        _log.debug(`File to validate: \n${JSON.stringify(tempo)}`);
                        implement(POTAppConfigValidator)(response.body); // check if object maps the interface
                        POTAppConfig.settings = response.body;
                        _log.debug(`Loaded file: \n${JSON.stringify(POTAppConfig.settings)}`);
                        POTAppConfig.logFactory.getLogger('log.app.config').info(
                            `Running on ${environment.name} mode
                          with ${POTAppConfig.settings.domain} domain and ${POTAppConfig.settings.basePath} basePath`
                        );
                        resolve();
                    } catch (error) {
                        reject(`Could not load file '${jsonFile}'. ${error}`);
                    }
                } else {
                    reject(`Could not load file '${jsonFile}'. Got a ${response.status} response.`);
                }
            });
        });
    }

    private setLogFactory(): void {
        const options: LoggerFactoryOptions = new LoggerFactoryOptions();
        // Set default logLevel for custom logger
        let logLevel: LogLevel = LogLevel.Info;
        // If boolean not set on config, default will be used (info)
        if (environment.debug) {
            logLevel = LogLevel.Debug;
        }
        // Add rules to custom logger with pattern 'file.*'
        options.addLogGroupRule(
            new LogGroupRule(new RegExp('log.+'),
                logLevel,
                new LogFormat(),
                LoggerType.Custom,
                // tslint:disable-next-line:max-line-length
                (name: string, logGroupRuntimeSettings: LogGroupRuntimeSettings) => new POTLoger(name, logGroupRuntimeSettings)
            ));
        POTAppConfig.logFactory = LFService.createNamedLoggerFactory('SEO-POT_LOG', options);
    }

    ngOnDestroy() {
    }
}
