import { POTGenericError } from './../utils/POT.errors';
import { POTHttpRequestError, POTValidationError } from '@appUtils/POT.errors';
import { POTModalDefaultMessages, POTModalStatusTypes, POTAvailableErrorTypes } from '@appUtils/POT.enums';
import { POTModalService } from '@appServices/POT.modal.service';
import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpResponse, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { timeout, map, catchError } from 'rxjs/operators';
import { POTStorageService } from '@appServices/POT.storage.service';

export const POTHttpTimeout = 10; // Secs

@Injectable()
export class POTTimeoutInterceptor implements HttpInterceptor {
    constructor() { }
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(req).pipe(timeout(POTHttpTimeout * 1000));
    }
}

@Injectable()
export class POTRequestInterceptor implements HttpInterceptor {
    constructor(public modalService: POTModalService, private storageService: POTStorageService) { }
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        let request = req;
        if (req instanceof HttpRequest && req.headers.has('POT_intercept-request')) {
            let headersToSend = new HttpHeaders();
            headersToSend = req.headers;
            headersToSend = headersToSend.delete('POT_intercept-request');
            if (!this.modalService.isActiveWaiting()) {
                this.modalService.openPopup(POTModalDefaultMessages.WAIT, POTModalStatusTypes.WAITING);
            }
            if (req.headers.has('POT_send-token')) {
                headersToSend = headersToSend.delete('POT_send-token');
                try {
                    if (req.headers.get('POT_send-token') === 'true') {
                        const activeToken = this.storageService.activeToken;
                        headersToSend = headersToSend.set('Authorization', `Bearer ${activeToken}`);
                    }
                } catch (error) {
                    throw error;
                }
            }
            request = req.clone({ headers: headersToSend });
        }
        return next.handle(request);
    }
}

@Injectable()
export class POTResponseInterceptor implements HttpInterceptor {
    constructor(public modalService: POTModalService) { }
    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        return next.handle(req).pipe(map(event => {
            if (event instanceof HttpResponse) {
                setTimeout(() => {
                    if (this.modalService.isActiveWaiting()) {
                        this.modalService.closeModal();
                    }
                }, 3 * 1000);
            }
            return event;
        }), catchError((error) => {
            if (this.retrowError(error)) {
                return throwError(error);
            }
            return throwError(this.buildPOTError(error.error));
        }));
    }

    private isServerSendingControledError(data: any): boolean {
        return data.internalResponseCode && data.internalResponseCode === 200;
    }

    private retrowError(error): boolean {
        if (this.isServerSendingControledError(error.error)) {
            return false;
        }
        return true;
    }

    private buildPOTError(error) {
        const informIfError: Array<number> = [405, 500];
        if (informIfError.includes(error.httpResponseCode)) {
            return new POTGenericError(error.message);
        }
        return new POTHttpRequestError(error.message);
    }
}
