import { POTIEndpoint, POTEndpointValidator } from '@appInterfaces/POT.endpoint.interface';
import {Interface, type} from '@appAssets/externals/implement';
/**
 * Interface used to bind the users group that contains the endPoints.
 */
export interface POTIUsersGroup {
    login: POTIEndpoint;
    createUser: POTIEndpoint;
    readUser: POTIEndpoint;
    replaceUser: POTIEndpoint;
    deleteUser: POTIEndpoint;
    getAllUsers: POTIEndpoint;
}
/**
 * Used to validate if a object implements the {@link POTIUsersGroup} interface. If it doesn't it will throw an Error.
 *
 * Also uses: {@link POTEndpointValidator}
 */
export const POTUsersGroupValidator = Interface('POTIUsersGroup')({
    login: type('object', POTEndpointValidator),
    createUser: type('object', POTEndpointValidator),
    readUser: type('object', POTEndpointValidator),
    replaceUser: type('object', POTEndpointValidator),
    deleteUser: type('object', POTEndpointValidator),
    getAllUsers: type('object', POTEndpointValidator),
}, { error: true, trim: true });
/**
 * Interface used to bind the available endPoint groups.
 */
export interface POTIEndpointGroups {
    user: POTIUsersGroup;
}
/**
 * Used to validate if a object implements the {@link POTIEndpointGroups} interface. If it doesn't it will throw an Error.
 *
 * Also uses: {@link POTUsersGroupValidator}
 */
export const POTEndpointGroupsValidator = Interface('POTIEndpointGroups')({
    users: type('object', POTUsersGroupValidator)
}, { error: true, trim: true });
