import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ModalData } from '@appInyectables/POT.modal-data.inyectable';
import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { POTFade, POTResize } from '@appUtils/POT.animations';
import { Observable } from 'rxjs';
import { POTModalConfiguration } from '@appModels/POT.modal-configuration.model';
/**
 * Components to allow creation of Popups, and modals with components as content.
 */
@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss'],
  animations: [POTResize, POTFade]
})
export class ModalComponent implements OnInit {
  @Input() configuration: Observable<POTModalConfiguration>;
  private dataObservable: Observable<POTModalConfiguration> = null;
  /**
   * Constructor with one app's injector and one ng-bootstrap injector to controll the active modal.
   * @param  {ModalData} private ModalData
   * @param  {NgbActiveModal} privaten gbActiveModal
   */
  constructor(private modalCommonData: ModalData, private ngbActiveModal: NgbActiveModal) { }

  ngOnInit() {
    this.dataObservable = this.modalCommonData.configuration;
  }
  /**
   * @returns A instance of the inyectable that contains the data
   */
  get modalData(): ModalData {
    return this.modalCommonData;
  }

  get data(): Observable<POTModalConfiguration> {
    return this.dataObservable;
  }

  dismissModal(): void {
    this.ngbActiveModal.dismiss();
  }
}
