import { ModalComponent } from '@appComponents/modal/modal.component';
import { POTModalError } from '@appUtils/POT.errors';
import { ModalData } from '@appInyectables/POT.modal-data.inyectable';
import { POTModalTypes, POTModalStatusTypes, POTModalAvailableComponents, POTModalDefaultMessages } from '@appUtils/POT.enums';
import { BehaviorSubject, Subject } from 'rxjs';
import { Injectable, OnDestroy } from '@angular/core';
import { NgbModal, NgbModalRef, NgbModalConfig, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { RegistrationComponent } from '@appComponents/users/registration/registration.component';
import { POTModalConfiguration } from '@appModels/POT.modal-configuration.model';

@Injectable({ providedIn: 'root' })
export class POTModalService implements OnDestroy {
    protected activeModalRef: NgbModalRef = null;
    protected activeSubject: BehaviorSubject<POTModalConfiguration> = null;
    protected isDefaultComponent = true;
    protected defaultOptions: NgbModalOptions = {
        size: 'lg',
        centered: true
    };

    constructor(private ngModalService: NgbModal, private commonData: ModalData) {
    }

    openPopup(message: string, status: POTModalStatusTypes): Promise<any> {
        const configuration = new POTModalConfiguration();
        configuration.status = status;
        configuration.message = message;
        return this.openModal(configuration);
    }

    openModal(configuration: POTModalConfiguration): Promise<any> {
        if (this.isValidModal(configuration)) {
            if (!this.isModalOpen()) {
                this.buildModal(configuration);
            } else {
                if (!this.isActiveCompatible(configuration)) {
                    this.changeActiveModal(configuration);
                }
                this.activeSubject.next(configuration);
            }
            return this.waitForResponse();
        }
        throw new POTModalError(`Cannot create modal, invalid data.`);
    }

    isActiveWaiting() {
        if (this.isModalOpen()) {
            return (this.commonData.activeConfiguration.status === POTModalStatusTypes.WAITING);
        }
        return false;
    }

    private buildModal(data: POTModalConfiguration): void {
        this.activeSubject = new BehaviorSubject<POTModalConfiguration>(data);
        this.commonData.setConfiguration(this.activeSubject.asObservable());
        const modalComponent = this.getComponentInstance(data.component);
        this.activeModalRef = (this.isClosable(data)) ?
            this.createClosableModal(modalComponent) : this.createUnclosablePopup(modalComponent);
        this.setResponseWhenDismissed();
    }

    private isActiveCompatible(data: POTModalConfiguration): boolean {
        const bothClosable = (this.activeModalRef && this.isActiveClosable() && this.isClosable(data) && this.isSameComponent(data));
        const bothUnclosable = (this.activeModalRef && !this.isActiveClosable() && !this.isClosable(data));
        return (bothClosable || bothUnclosable);
    }

    private changeActiveModal(data: POTModalConfiguration): void {
        this.closeActiveModalRef(false);
        this.buildModal(data);
    }

    private setResponseWhenDismissed() {
        this.activeModalRef.result.then().catch(() => {
            this.commonData.setResponseDISMISSED(null);
        });
    }

    isModalOpen(): boolean {
        const anyModalOpen = this.activeModalRef != null && this.ngModalService.hasOpenModals();
        return anyModalOpen;
    }

    setResponseOK(openNewPopup: boolean, message: string): Promise<any> {
        this.commonData.setResponseOK(message);
        this.closeActiveModalRef(false);
        if (openNewPopup) {
            let modalMessage = POTModalDefaultMessages.OK.toString();
            if (message !== null) {
                modalMessage = message;
            }
            this.openPopup(modalMessage, POTModalStatusTypes.OK);
            return this.waitForResponse();
        }
        return Promise.resolve();
    }

    setResponseKO(openNewPopup: boolean, message: string): Promise<any> {
        this.commonData.setResponseKO(message);
        this.closeActiveModalRef(false);
        if (openNewPopup) {
            let modalMessage = POTModalDefaultMessages.KO.toString();
            if (message !== null) {
                modalMessage = message;
            }
            this.openPopup(modalMessage, POTModalStatusTypes.KO);
            return this.waitForResponse();
        }
        return Promise.resolve();
    }

    closeModal(): void {
        this.closeActiveModalRef(true);
    }

    waitForResponse(): Promise<any> {
        return this.commonData.response.toPromise();
    }

    private isValidModal(data: POTModalConfiguration): boolean {
        return !(
            data.modalType === POTModalTypes.MODAL && data.component === POTModalAvailableComponents.DEFAULT &&
            (data.status === POTModalStatusTypes.OK || data.status === POTModalStatusTypes.KO)
        );
    }

    private isClosable(data: POTModalConfiguration): boolean {
        return !(data.modalType === POTModalTypes.POPUP && data.status === POTModalStatusTypes.WAITING);
    }

    private isActiveClosable(): boolean {
        let isClosable;
        this.commonData.configuration.subscribe((configuration) => {
            isClosable = this.isClosable(configuration);
        }).unsubscribe();
        return isClosable;
    }

    private isSameComponent(data: POTModalConfiguration): boolean {
        let activeComponent;
        this.commonData.configuration.subscribe((configuration) => {
            activeComponent = configuration.component;
        }).unsubscribe();
        return (data.component === activeComponent);
    }

    private createUnclosablePopup(component: any): NgbModalRef {
        const specificOptions = new NgbModalConfig();
        specificOptions.backdrop = 'static';
        specificOptions.keyboard = false;
        return this.ngModalService.open(component, Object.assign({}, this.defaultOptions, specificOptions));
    }


    private createClosableModal(component: any): NgbModalRef {
        return this.ngModalService.open(component, this.defaultOptions);
    }

    private getComponentInstance(componentType: POTModalAvailableComponents): any {
        switch (componentType) {
            case (POTModalAvailableComponents.REGISTRATION):
                this.isDefaultComponent = false;
                return RegistrationComponent;
        }
        this.isDefaultComponent = true;
        return ModalComponent;
    }

    private closeActiveModalRef(informResult: boolean): void {
        if (this.isModalOpen()) {
            this.activeModalRef.close();
            this.activeModalRef = null;
            if (informResult) {
                this.commonData.setResponseCLOSE(null);
            }
        } else {
            throw new Error(`[modalService.closeModal()]There's no active modal instance.`);
        }
    }

    ngOnDestroy() {
        this.activeSubject.complete();
        this.activeSubject = null;
    }
}
