import { POTAppConfig } from '@appInyectables/POT.app.config';
import { POTStorageService } from '@appServices/POT.storage.service';
import { POTUserModel } from '@appModels/POT.user.model';
import { Component, OnInit } from '@angular/core';
import { POTRouterService } from '@appServices/POT.router.service';
import { Observable } from 'rxjs';

/**
 * Component with basic navigation routes and options for the user such as logout.
 */
@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  loggedUser$: Observable<POTUserModel>;
  private log = POTAppConfig.logFactory.getLogger('log.component.navbar');
  /**
  * Flag to open the navbar on non-desktop devices.
  */
  private navbarOpen = false;
  /**
   * Constructor with one app's injector and the router injector for navigation.
   * @param  {RouterService} routerService
   * @param  {StorageService} storageService
   */
  constructor(private routerService: POTRouterService, private storageService: POTStorageService) { }
  /**
   * @returns {boolean} A flag to check if the navBar is open
   */
  isNavbarOpen(): boolean {
    return this.navbarOpen;
  }
  /**
   * Set's the component's obervable instance. This instance is obtained from {@link StorageService} injector.
   */
  ngOnInit() {
    try {
      this.loggedUser$ = this.storageService.loggedUser;
    } catch (error) {
      this.log.error(error);
      this.routerService.navigateTo('/login');
    }
  }
  /**
   * Changes the navbarOpen flag to open/close the navbar.
   */
  toggleNavbar() {
    this.navbarOpen = !this.navbarOpen;
  }
  /**
   * Remove the sessionStorage data and redirect to login.
   */
  logOut() {
    this.storageService.clearSessionStorage();
    this.routerService.navigateTo('/login');
  }
}
