import { MainComponent } from '@appComponents/main/main.component';
import { POTAuthGuard } from '@appInyectables/POT.auth-guard';
import { RuleDetailComponent } from '@appComponents/rules/rule-detail/rule-detail.component';
import { RuleListComponent } from '@appComponents/rules/rule-list/rule-list.component';
import { SiteDetailComponent } from '@appComponents/sites/site-detail/site-detail.component';
import { SiteListComponent } from '@appComponents/sites/site-list/site-list.component';
import { UserListComponent } from '@appComponents/users/user-list/user-list.component';
import { HomeComponent } from '@appComponents/users/home/home.component';
import { UserDetailComponent } from '@appComponents/users/user-detail/user-detail.component';
import { NgModule } from '@angular/core';
import { PageNotFoundComponent } from '@appComponents/page-not-found/page-not-found.component';
import { RouterModule, Route } from '@angular/router';
import { LoginComponent } from '@appComponents/users/login/login.component';
import { POTBaseRoutes } from '@appUtils/POT.enums';
/**
* Sets the available routes.
*/
const APP_ROUTES: Route[] = [
  { path: POTBaseRoutes.LOGIN, component: LoginComponent },
  { path: POTBaseRoutes.HOME, component: MainComponent, canActivate: [POTAuthGuard], children: [
    { path: ':id', component: HomeComponent},
    { path: '**', component: PageNotFoundComponent}
  ]},
  { path: POTBaseRoutes.USERS, component: MainComponent, canActivate: [POTAuthGuard], children: [
    { path: '', component: UserListComponent},
    { path: ':id', component: UserDetailComponent},
    { path: '**', component: PageNotFoundComponent}
  ]},
  { path: POTBaseRoutes.SITES, component: MainComponent, canActivate: [POTAuthGuard], children: [
    { path: '', component: SiteListComponent},
    { path: ':id', component: SiteDetailComponent},
    { path: '**', component: PageNotFoundComponent}
  ]},
  { path: POTBaseRoutes.RULES, component: MainComponent, canActivate: [POTAuthGuard], children: [
    { path: '', component: RuleListComponent},
    { path: ':id', component: RuleDetailComponent},
    { path: '**', component: PageNotFoundComponent}
  ]},
  { path: '', redirectTo: POTBaseRoutes.DEFAULT, pathMatch: 'prefix'}
];

@NgModule({
  imports: [
    RouterModule.forRoot(
      APP_ROUTES,
      { enableTracing: false } // <-- debugging purposes only
    )
  ],
  providers: [POTAuthGuard],
  exports: [
    RouterModule
  ]
})
export class POTRoutingModule { }
