import { POTAppConfig } from '@appInyectables/POT.app.config';
import { HttpClient, HttpHeaders, HttpResponse, } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { POTIEndpoint } from '@appInterfaces/POT.endpoint.interface';
import { POTAvailableHttpMethods } from '@appUtils/POT.enums';
import { POTPromiseError } from '@appUtils/POT.errors';

@Injectable({ providedIn: 'root' })
export class POTRestService {
    protected log = POTAppConfig.logFactory.getLogger('log.service.rest');

    constructor(private http: HttpClient) { }

    call(endPoint: POTIEndpoint, params: any, data: any): Promise<HttpResponse<any>> {
        let customHeaders = new HttpHeaders();
        customHeaders = customHeaders.set('POT_intercept-request', 'true');
        let sendToken = false;
        if (endPoint.requiresAuth) {
            sendToken = true;
        }
        customHeaders = customHeaders.set('POT_send-token', sendToken.toString());
        const targetUrl = this.buildTargetUrl(endPoint, params);
        if (endPoint.mocked) {
            this.log.debug(`Simulating ${endPoint.httpMethod} call on url: ${targetUrl}`);
            return this.doGet(endPoint.mockLocation, customHeaders);
        }
        switch (endPoint.httpMethod) {
            case POTAvailableHttpMethods.POST:
                return this.doPost(targetUrl, data, customHeaders);
            case POTAvailableHttpMethods.GET:
                return this.doGet(targetUrl, customHeaders);
            case POTAvailableHttpMethods.PUT:
                return this.doPut(targetUrl, data, customHeaders);
            case POTAvailableHttpMethods.DELETE:
                return this.doDelete(targetUrl, customHeaders);
            default:
                throw new POTPromiseError('HttpMethod not available');
        }
    }

    private buildTargetUrl(endPoint: POTIEndpoint, params: any): string {
        const targetDomain = `${POTAppConfig.settings.domain}/${POTAppConfig.settings.basePath}/`;
        let targetUrl = endPoint.url;
        const regEx = new RegExp('\{(.*?)\}', 'g');
        const urlParamsWithBraces = endPoint.url.match(regEx);
        if (urlParamsWithBraces !== null && params !== null) {
            urlParamsWithBraces.forEach(element => {
                let elementWithoutBraces = element;
                elementWithoutBraces = elementWithoutBraces.replace('{', '');
                elementWithoutBraces = elementWithoutBraces.replace('}', '');
                const parameter = params[elementWithoutBraces];
                if (parameter) {
                    this.log.debug(`Replacing ${element} with value ${parameter}`);
                    targetUrl = targetUrl.replace(element, parameter);
                }
            });
        }
        return targetDomain + targetUrl;
    }

    private doPost(url: string, data: any, headersToSend: HttpHeaders): Promise<HttpResponse<any>> {
        this.log.debug(`Doing post on ${url} with data: ${JSON.stringify(data)} and headers: ${JSON.stringify(headersToSend)}`);
        return this.http.post<any>(url, data, { headers: headersToSend, observe: 'response' }).toPromise();
    }

    private doGet(url: string, headersToSend: HttpHeaders): Promise<HttpResponse<any>> {
        this.log.debug(`Doing get on ${url} with headers: ${JSON.stringify(headersToSend)}`);
        return this.http.get<any>(url, { headers: headersToSend, observe: 'response' }).toPromise();
    }

    private doPut(url: string, data: any, headersToSend: HttpHeaders): Promise<HttpResponse<any>> {
        this.log.debug(`Doing put on ${url} with data: ${JSON.stringify(data)} and headers: ${JSON.stringify(headersToSend)}`);
        return this.http.put<any>(url, data, { headers: headersToSend, observe: 'response' }).toPromise();
    }

    private doDelete(url: string, headersToSend: HttpHeaders): Promise<HttpResponse<any>> {
        this.log.debug(`Doing delete on ${url} with headers: ${JSON.stringify(headersToSend)}`);
        return this.http.delete<any>(url, { headers: headersToSend, observe: 'response' }).toPromise();
    }
}
