import { ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { POTUserModel } from '@appModels/POT.user.model';
import { POTRouterService } from '@appServices/POT.router.service';
import { POTUserService } from '@appServices/POT.user.service';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { Component, OnInit, OnDestroy, ChangeDetectorRef, AfterViewChecked } from '@angular/core';
import { POTStorageService } from '@appServices/POT.storage.service';
import { POTFormValidators } from '@appUtils/POT.validators';
import { Observable } from 'rxjs';

/**
 * Shows user's data
 */
@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.scss']
})
// TODO: Refactor all this component
export class UserDetailComponent implements OnInit, OnDestroy, AfterViewChecked {
  loggedUser$: Observable<POTUserModel>;
  isLoggedActive = false;
  isEditingEmail = false;
  isEditingName = false;
  isActiveAdmin = false;
  isEditingRole = false;
  isEditingPassword = false;
  showEditButton = false;
  activeUser: POTUserModel;
  userDetailForm: FormGroup;
  /**
 * @ignore
 */
  protected log = POTAppConfig.logFactory.getLogger('log.component.user-detail');
  constructor(
    private formBuilder: FormBuilder,
    private cdRef: ChangeDetectorRef,
    private storageService: POTStorageService,
    private userService: POTUserService,
    private activeRoute: ActivatedRoute) {
      this.loggedUser$ = this.storageService.loggedUser;
      let loggedUser: POTUserModel;
      this.storageService.loggedUser.subscribe((loggedUserValue) => {
        loggedUser = loggedUserValue;
      });
      let usernameOnUrl: string = null;
      this.activeRoute.params.subscribe((paramsValue) => {
        this.clearInputData();
        usernameOnUrl = paramsValue.id;
        if (loggedUser.username === usernameOnUrl) {
          this.activeUser = loggedUser;
          this.isLoggedActive = true;
          this.isActiveAdmin = (this.activeUser && (this.activeUser.role === 'admin'));
        } else {
          this.userService.getUser(usernameOnUrl).then((userValue) => {
            this.activeUser = userValue;
            this.isActiveAdmin = (this.activeUser && (this.activeUser.role === 'admin'));
          });
        }
      });
  }
  /**
   * @ignore
   */
  ngOnInit() {
    this.userDetailForm = this.formBuilder.group({
      nameInput: [null, [Validators.required, Validators.minLength(2)]],
      emailInput: [null, [Validators.required, Validators.compose([Validators.minLength(5), Validators.email])]],
      passwordInput: [null, Validators.compose([
        Validators.required,
        Validators.minLength(8),
        // check whether the entered password has a number
        POTFormValidators.regExValidation(/\d/, {
          hasNumber: true
        }),
        // check whether the entered password has upper case letter
        POTFormValidators.regExValidation(/[A-Z]/, {
          hasCapitalCase: true
        }),
        // check whether the entered password has a lower case letter
        POTFormValidators.regExValidation(/[a-z]/, {
          hasSmallCase: true
        }),
        // check whether the entered password has a special character
        POTFormValidators.regExValidation(
          /[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
          {
            hasSpecialCharacters: true
          }
        )
      ])],
      repeatPasswordInput: [null, Validators.compose([Validators.required])]
    }, { validator: POTFormValidators.samePasswordValidation });
  }

  private clearInputData() {
    if (this.userDetailForm) {
      this.userDetailForm.get('emailInput').setValue('');
      this.userDetailForm.get('nameInput').setValue('');
      this.userDetailForm.get('passwordInput').setValue('');
      this.userDetailForm.get('repeatPasswordInput').setValue('');
    }
  }

  ngAfterViewChecked() {
    this.cdRef.detectChanges();
  }

  get fControl(): { [key: string]: AbstractControl; } { return this.userDetailForm.controls; }

  get isEditing(): boolean {
    const emailInput = (this.userDetailForm.get('emailInput')) ? this.userDetailForm.get('emailInput').value : null;
    this.isEditingEmail = emailInput !== null && emailInput !== '';

    const nameInput = (this.userDetailForm.get('nameInput')) ? this.userDetailForm.get('nameInput').value : null;
    this.isEditingName = nameInput !== null && nameInput !== '';
    return this.isEditingEmail || this.isEditingName || this.isEditingRole || this.isEditingPassword;
  }

  get validForm(): boolean {
    const validEmail = !this.isEditingEmail ||
      (this.isEditingEmail && this.userDetailForm.get('emailInput').valid);
    const validName = !this.isEditingName ||
      (this.isEditingName && this.userDetailForm.get('nameInput').valid);
    const passData = (this.userDetailForm.get('passwordInput')) ? this.userDetailForm.get('passwordInput').value : null;
    const validPassData = passData !== null && this.userDetailForm.get('passwordInput').valid;
    const repeatPass = (this.userDetailForm.get('repeatPasswordInput')) ? this.userDetailForm.get('repeatPasswordInput').value : null;
    const validRepeatData = repeatPass !== null && this.userDetailForm.get('repeatPasswordInput').valid;
    const validPassword = !this.isEditingPassword || (validPassData && validRepeatData);
    return validEmail && validName && validPassword;
  }

  changeRole() {
    this.isEditingRole = !this.isEditingRole;
    this.isActiveAdmin = !this.isActiveAdmin;
  }

  changeData() {
    if (this.isEditing) {
      if (!this.validForm) {
        this.log.error('Invalid data on form, please try again.');
        return;
      }
      const newUserData = this.buildNewUserData();
      if (this.isLoggedActive) {
        this.changeLoggedUserData(newUserData);
      } else {
        this.changeUserData(newUserData);
      }
    } else {
      this.log.error('No edited data to send');
    }
  }

  private buildNewUserData(): POTUserModel {
    const newUserData = new POTUserModel();
    newUserData.username = this.activeUser.username;
    newUserData.email = (this.userDetailForm.get('emailInput').value) ? this.userDetailForm.get('emailInput').value : this.activeUser.email;
    newUserData.name = (this.userDetailForm.get('nameInput').value) ? this.userDetailForm.get('nameInput').value : this.activeUser.name;
    newUserData.password =
    (this.userDetailForm.get('passwordInput').value) ? this.userDetailForm.get('passwordInput').value : this.activeUser.password;
    newUserData.role = (this.isActiveAdmin) ? 'admin' : 'user';
    return newUserData;
  }

  private changeLoggedUserData(newData: POTUserModel) {
    this.log.debug(`Editing logged user: ${this.activeUser.username}`);
    this.storageService.loggedUser = newData;
    this.changeUserData(newData);
  }

  private changeUserData(newData: POTUserModel) {
    this.log.debug(`Calling service to edit user: ${this.activeUser.username}`);
    this.userService.replaceUser(this.activeUser.username, newData).then(() => {
      this.clearInputData();
      this.activeUser = newData;
      this.cdRef.detectChanges();
    });
  }
  /**
   * @ignore
   */
  ngOnDestroy() {
  }
}
