import { POTIDeserializable } from '@appInterfaces/POT.deserialize.interface';
/**
* UserModel
*/
export class POTUserModel implements POTIDeserializable {
    email: string;
    username: string;
    password: string;
    name: string;
    role: string;
    uri: string;
    tokenValue: string;
    deserialize(input: any) {
        Object.assign(this, input);
        return this;
    }
}
