import { POTEndpointGroupsValidator } from '@appInterfaces/POT.groups.interface';
import { POTIUsersGroup } from '@appInterfaces/POT.groups.interface';
import {Interface, type} from '@appAssets/externals/implement';

/**
 * Interface used to bind the configuration file that contains the endPoints definitions
 */
export interface POTIAppConfig {
    domain: string;
    basePath: string;
    endPoints: {
        users: POTIUsersGroup
    };
}

/**
 * Used to validate if a object implements the {@link POTIAppConfig} interface. If it doesn't it will throw an Error.
 *
 * Also uses: {@link POTEndpointGroupsValidator}
 */
export const POTAppConfigValidator = Interface('POTIAppConfig')({
    domain: type('string'),
    basePath: type('string'),
    endPoints: type('object', POTEndpointGroupsValidator)
}, { error: true, trim: true });
