import { POTStorageService } from '@appServices/POT.storage.service';
import { POTUserModel } from '@appModels/POT.user.model';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { POTRestService } from '@appServices/POT.rest.service';
import { Injectable, OnDestroy, OnInit } from '@angular/core';
import { POTValidationError } from '@appUtils/POT.errors';
import { Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class POTUserService implements OnInit, OnDestroy {
    protected $loggedUser: Observable<POTUserModel> = null;
    protected envSettings = POTAppConfig.settings;
    protected log = POTAppConfig.logFactory.getLogger('log.service.user');

    constructor(private restService: POTRestService, private storageService: POTStorageService) { }

    ngOnInit() { }

    async login(user: POTUserModel): Promise<void> {
        this.log.debug(`Loggin in with: ${user.username} , ${user.password}`);

        const loginResponse = await this.restService.call(this.envSettings.endPoints.users.login, null, user).catch((error) => {
            this.log.error(`[login] ${error}`);
            throw error;
        });
        const loginModel = new POTUserModel().deserialize(loginResponse.body);
        if (!loginModel || !loginModel.tokenValue || !loginModel.username) {
            throw new POTValidationError('Token or username not found on server response.');
        }
        this.storageService.activeToken = loginModel.tokenValue;
        const getUserResponse = await this.getUser(loginModel.username);
        this.storageService.loggedUser = getUserResponse;
        Promise.resolve();
    }

    registration(user: POTUserModel): Promise<void> {
        this.log.debug(`Registering new user: ${user.username}`);
        return new Promise<void>((resolve) => {
            this.restService.call(this.envSettings.endPoints.users.createUser, user, null).then(() => {
                resolve();
            }).catch((error: any) => {
                this.log.error(error);
                throw error;
            });
        });
    }

    async getUser(username: string): Promise<POTUserModel> {
        this.log.debug(`Getting user with id: ${username}`);
        const params = {
            username: username
        };
        const readUserResponse = await this.restService.call(this.envSettings.endPoints.users.readUser, params, null).catch(
            (error) => { throw error; }
        );
        const readedUser = new POTUserModel().deserialize(readUserResponse.body); // TODO: Validate the userModel
        return readedUser;
    }

    async getUsers(): Promise<Array<POTUserModel>> {
        this.log.debug(`Getting user list`);
        const userList: Array<POTUserModel> = [];
        const dirtyList = await this.restService.call(this.envSettings.endPoints.users.getAllUsers, null, null).catch((error) => {
            this.log.error(error);
            throw error;
        });
        dirtyList.body.forEach(element => {
            userList.push(new POTUserModel().deserialize(element));
        });
        return userList;
    }

    async deleteUser(userId: string): Promise<void> {
        this.log.debug(`Deleting user with id: ${userId}`);
        const params = {
            username: userId
        };
        const response = await this.restService.call(this.envSettings.endPoints.users.deleteUser, params, null).catch((error) => {
            this.log.error(error);
            throw error;
        });
        return Promise.resolve();
    }

    async replaceUser(userId: string, newUserData: POTUserModel): Promise<void> {
        this.log.debug(`Replacing user with id: ${userId} and new data: ${JSON.stringify(newUserData)}`);
        const params = {
            username: userId
        };
        const data = newUserData;
        const response = await this.restService.call(this.envSettings.endPoints.users.replaceUser, params, data).catch((error) => {
            this.log.error(error);
            throw error;
        });
        return Promise.resolve();
    }

    logOut(): void {
        this.storageService.clearSessionStorage();
        this.storageService.clearLocalStorage();
    }

    ngOnDestroy() {
    }
}
