import { POTUserModel } from '@appModels/POT.user.model';
import { Subject, Observable, BehaviorSubject, merge } from 'rxjs';
import { Injectable, OnDestroy } from '@angular/core';
import { SessionStorageService, LocalStorageService } from 'ngx-store';
import { POTConfigurationError } from '@appUtils/POT.errors';
import { filter, takeUntil, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class POTStorageService implements OnDestroy {
  private unsubscribe: Subject<any> = new Subject<any>();
  constructor(private sessionStorageService: SessionStorageService, private localStorageService: LocalStorageService) { }

  private getMainObservable(key: string): Observable<any> {
    const actualValue = this.sessionStorageService.get(key);
    if (actualValue) {
      const onChangeObservable = this.sessionStorageService.observe(key)
        .pipe(filter(event => event.isInternal))
        .pipe(takeUntil(this.unsubscribe))
        .pipe(map(event => {
          return event.newValue;
        }));
      const actualValueObservable = new BehaviorSubject(actualValue).asObservable();
      return merge(actualValueObservable, onChangeObservable);
    }
    throw new POTConfigurationError(`Could not find ${key} on storage.`);
  }

  clearSessionStorage() {
    this.sessionStorageService.clear();
  }

  clearLocalStorage() {
    this.localStorageService.clear();
  }

  get loggedUser(): any {
    try {
      return this.getMainObservable('loggedUser');
    } catch (error) {
      throw error;
    }
  }

  set loggedUser(value: any) {
    if (this.sessionStorageService.get('loggedUser')) {
      this.sessionStorageService.update('loggedUser', value);
    } else {
      this.sessionStorageService.set('loggedUser', new POTUserModel().deserialize(value));
    }
  }

  get activeToken(): string {
    return this.sessionStorageService.get('activeToken');
  }

  set activeToken(value: string) {
    this.sessionStorageService.set('activeToken', value);
  }

  ngOnDestroy() {
    this.unsubscribe.next();
    this.unsubscribe.unsubscribe();
  }

}
