import { POTModalStatusTypes, POTAvailableErrorTypes, POTModalDefaultMessages } from '@appUtils/POT.enums';
import { POTModalService } from '@appServices/POT.modal.service';
import { Injectable, ErrorHandler } from '@angular/core';
import * as Sentry from '@sentry/browser';

Sentry.init({
    // dsn: 'https://7cdafbae6c9e4d269c8e552be6d6322c@sentry.io/1391806'
});

@Injectable()
export class POTErrorInterceptor implements ErrorHandler {
    constructor(private modalService: POTModalService) { }
    handleError(error) {
        let sendToSentry = false;
        let showPopup = false;
        let modalMessage = POTModalDefaultMessages.KO.toString();
        const errorToCheck = (error.rejection) ? error.rejection : error;
        switch (errorToCheck.name) {
            case POTAvailableErrorTypes.ConfigurationError:
                sendToSentry = true;
                showPopup = true;
                modalMessage = 'Looks like a bad recipe. An configuration error has ocurred, please try again later.';
                break;
            case POTAvailableErrorTypes.GenericError:
                sendToSentry = true;
                showPopup = true;
                break;
            case POTAvailableErrorTypes.HttpRequestError:
                sendToSentry = false;
                showPopup = true;
                if (errorToCheck.message) {
                    modalMessage = errorToCheck.message;
                }
                break;

            case POTAvailableErrorTypes.PromiseError:
                sendToSentry = true;
                showPopup = false;
                if (errorToCheck.message) {
                    modalMessage = errorToCheck.message;
                }
                break;
            case POTAvailableErrorTypes.ValidationError:
                sendToSentry = false;
                showPopup = true;
                if (errorToCheck.message) {
                    modalMessage = errorToCheck.message;
                }
                break;
            case POTAvailableErrorTypes.ModalError:
                sendToSentry = false;
                showPopup = false;
                break;
            case 'TimeoutError':
                sendToSentry = true;
                showPopup = true;
                modalMessage = 'UPS! Looks like you have been waiting too much.' +
                'If you were updating data, please check later if the update has made.';
                console.error(errorToCheck);
                break;
            default:
                sendToSentry = true;
                showPopup = true;
                console.error(errorToCheck);
                break;
        }
        if (showPopup) {
            this.modalService.openPopup(modalMessage, POTModalStatusTypes.KO);
        }
        if (sendToSentry) {
            console.error(`Sending to sentry an ${errorToCheck.name}`);
            // Sentry.captureException(error);
        }
    }
}
