import { POTStorageService } from '@appServices/POT.storage.service';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { POTUserModel } from '@appModels/POT.user.model';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
/**
 * Component used to show a general view of the user data such as sites, etc.
 */
export class HomeComponent implements OnInit, OnDestroy {
  loggedUser$: Observable<POTUserModel>;
  private counter = 0;

  /**
   * @param  {StorageService} storageService
   */
  constructor(private storageService: POTStorageService) { }

  ngOnInit() {
    this.loggedUser$ = this.storageService.loggedUser;
  }

  ngOnDestroy() { }
}
