import { POTRouterService } from '@appServices/POT.router.service';
import { POTUserModel } from '@appModels/POT.user.model';
import { POTUserService } from '@appServices/POT.user.service';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { POTStorageService } from '@appServices/POT.storage.service';
import { BehaviorSubject, Subject, of } from 'rxjs';
import { switchMap } from 'rxjs/operators';
/**
 * Shows user list
 */
@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss']
})
export class UserListComponent implements OnInit, OnDestroy {
  public loggedUser: POTUserModel = null;
  private _search$: Subject<void> = new Subject();
  private _fullUserList: POTUserModel[] = [];
  private _userList$: BehaviorSubject<POTUserModel[]> = new BehaviorSubject(null);
  private _listTotal$: BehaviorSubject<number> = new BehaviorSubject(0);
  private _searchState = {
    page: 1,
    pageSize: 4,
    searchTerm: ''
  };

  /**
 * @ignore
 */
  protected log = POTAppConfig.logFactory.getLogger('log.component.user-list');
  constructor(private userService: POTUserService, private storageService: POTStorageService, private routerService: POTRouterService) {
    const loggedUserObservable = this.storageService.loggedUser;
    loggedUserObservable.subscribe((value) => {
      this.loggedUser = value;
    });
    this.userService.getUsers().then((value) => {
      this._fullUserList = value;
      this.updateUserListData();
    });
  }

  ngOnInit() {
  }

  get userList$() { return this._userList$.asObservable(); }
  get listTotal$() { return this._listTotal$.asObservable(); }
  get page() { return this._searchState.page; }
  get pageSize() { return this._searchState.pageSize; }
  get searchTerm() { return this._searchState.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }

  private _set(patch: Partial<any>) {
    Object.assign(this._searchState, patch);
    this._search$.next();
  }

  isActiveUser(user: POTUserModel): boolean {
    return user.username === this.loggedUser.username;
  }

  goToUser(user: POTUserModel) {
    this.routerService.navigateTo(`users/${user.username}`);
  }

  deleteUser(user: POTUserModel) {
    this.userService.deleteUser(user.username).then( _ => {
      const posToRemove = this._fullUserList.findIndex(userToCheck => userToCheck.username === user.username);
      this._fullUserList.splice(posToRemove, 1);
      this.updateUserListData();
    });
  }

  private updateUserListData() {
    this._search$.pipe(
      switchMap(() => this.search())
    ).subscribe(result => {
      this._userList$.next(result.userList);
      this._listTotal$.next(result.total);
    });
    this._search$.next();
  }

  private search() {
    // Search filter
    let userList = this._fullUserList.filter(user => this.matches(user, this.searchTerm));
    const total = userList.length;
    // Paginate
    userList = userList.slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
    return of({ userList, total });
  }

  private matches(user: POTUserModel, term: string): boolean {
    return user.username.toLowerCase().includes(term)
      || user.email.toLowerCase().includes(term)
      || user.name.toLowerCase().includes(term);
  }

  ngOnDestroy() { }
}
