import { SessionStorage } from 'ngx-store';
import { POTAppConfig } from '@appInyectables/POT.app.config';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Injectable } from '@angular/core';
import { POTBaseRoutes } from '@appUtils/POT.enums';
import { POTUserModel } from '@appModels/POT.user.model';
import { Observable } from 'rxjs';

@Injectable({ providedIn: 'root' })
export class POTRouterService {
    protected log = POTAppConfig.logFactory.getLogger('log.service.router');
    @SessionStorage() loggedUser: POTUserModel;
    protected envSettings = POTAppConfig.settings;
    constructor(private router: Router, private activeRoute: ActivatedRoute) { }

    get currentURL(): string {
        return this.router.url;
    }

    get domain(): string {
        return this.envSettings.domain;
    }

    get basePath(): string {
        return this.envSettings.basePath;
    }

    get defaultRedirectionRoute(): string {
        return POTBaseRoutes.DEFAULT;
    }

    observeParams(): Observable<Params> {
        return this.activeRoute.params;
    }

    navigateTo(uri: string): void {
        this.router.navigate([uri]);
    }

    redirectToHome(): void {
        this.router.navigate([POTBaseRoutes.HOME + '/' + this.loggedUser.username]);
    }
}
